import Vue from 'vue'
import Vuex from 'vuex'
import creatPersistedState from 'vuex-persistedstate'
import Router from 'vue-router'
Vue.use(Vuex)
export default new Vuex.Store({
    strict:true,
    plugins:[creatPersistedState()],
    state:{
        user:null,
        loginstatus:false,
        userInView: null,
        popUserInView:null,
        TAfollowing:null
    },
    mutations:{
        setUser(state,user){
            state.user = user
            state.loginstatus = true
            //Router().push('dashboard')
        },
        logout(state, logout){
            state.user = null
            state.loginstatus = false
            //Router().push('home')
        },
        setUserInView(state,data){
            state.userInView = data.user,
            state.TAfollowing = data.TAfollowing,
            state.popUserInView = data.populatedUser
        }
    },
    actions:{
        setUser({commit},user){
            console.log(user)
            commit('setUser', user)
        },
        logout({commit}, logout){
            commit('logout', logout)
        },
        setUserInView({commit}, data){
            commit('setUserInView', data)
        }
    }
})


