<template>
	<b-container class="register">
		<b-row>
			<b-col sm='8' offset-sm="2">
				<h3 class="text-center">Create New Article</h3>
				<h4>Creating...</h4>
				<b-form @submit.prevent="newArticle" v-if="show" enctype='multipart/form-data'>

					<b-form-group	label="Article Title:"	label-for="Input2">
						<b-form-input id="Input2" type="text"	v-model="form.title"	required	placeholder="Enter article title"></b-form-input>
					</b-form-group>
					<div>
						<b-form-textarea id="textarea1" v-model="form.body" placeholder="Write Article" :rows="3" :max-rows="6"></b-form-textarea>
						
					</div>

					<div>
						<b-form-select v-model="form.category" :options="options" class="mb-3" />
					</div>
					<b-form-group>
						<b-form-file @change="handleUpload($event.target.files)"  placeholder="Upload a cover..."></b-form-file>
					</b-form-group>

					<b-button type="submit" variant="primary">Submit</b-button>
				</b-form>
			</b-col>
		</b-row>
	</b-container>
</template>

<script>
import api from '@/services/api'
export default {
  data () {
    return {
			form:{
				title:'',
				body:'',
				imgSrc:'',
				category:null
			},
      options: [
        { value: null, text: 'Select a category' },
        { value: 'health', text: 'Health and Safty' },
        { value: 'religion-and-spirituality', text: 'Religion and Spirituality' },
        { value: 'politics', text: 'Politics' },
        { value: 'tech', text: 'Tech' }
			],
			show:true
    }
	},
	methods:{
		handleUpload(files){
			this.form.imgSrc = files[0]
		},
		async newArticle(){
			try {
				this.show = false
				const formData = new FormData
				formData.append('title', this.form.title)
				formData.append('body', this.form.body)
				formData.append('imgSrc', this.form.imgSrc)
				formData.append('category', this.form.category)
				formData.append('author', this.$store.state.user._id)

				let response = await api().post(`profile/${this.$store.state.user.username}/create`, formData)
				console.log(response.data)
				this.$router.push(`/profile/${this.$store.state.user.username}/articles`)
			} catch (error) {
				console.log(`error creating new article ====> ${error}`)
			}
			
		}
	}
}
</script>

<style lang="scss" scoped>

</style>



