<template>
	<b-container class="login">
		<b-row>
			<b-col sm='8' offset-sm="2">
				<h3 class="text-center">Login</h3>
				<b-form @submit="login" v-if="show">
					<b-form-group	label-for="Input1">
						<b-form-input id="Input1"
							type="text" v-model="form.username" required placeholder="Enter username">
						</b-form-input>
					</b-form-group>

					<b-form-group 	label-for="Input2">
						<b-form-input id="Input2"	type="password"	v-model="form.password"	required	placeholder="********">	</b-form-input>
					</b-form-group>
					
					<b-button type="submit" variant="primary">Submit</b-button>
				</b-form>
			</b-col>
		</b-row>
	</b-container>
</template>
<script>
import authenticationService from '@/services/authenticationService'
export default {
  data(){
	  return{
		  form:{
			  username:'',
			  password:''
		  },
		  show:true,
	  }
  },
  methods:{
	  async login (evt) {
		try{
			evt.preventDefault(); // prevent default form behaviour on submit
			this.show = !this.show // hide the form
			console.log(JSON.stringify(this.form));
			const response = await authenticationService.login('users/login', this.form) // get user data from server
			console.log(response.data)
			this.$store.dispatch('setUser', response.data)  // 
			this.$router.push('/profile/' + response.data.username)
		}
		catch(error){
			console.log('error ' + error)
		}
	}
  }
}
</script>
<style lang="scss" scoped>
	.login{
		margin-top: 20px;
	}
</style>





