<template>
  <div>
      <b-container class="fluid">
				<b-row>
					<b-col sm="10" offset-sm="1" class="home">
						<h1 class='text-center'>{{title}}</h1>
						<hr>
						<b-row>
							<b-col sm="8" class="main">
								<div v-for='post in posts' :key='post.id' class='posts'>
									<b-card class='card' style='background-color:purpl;min-height:200px;' img-src="./static/noImg.png">
										<div class="card-text">
											<h4>
												<a v-bind:href="'/#/articles/' + post._id">{{post.title}}</a>
											</h4>
											<p>{{post.body}}</p>
											<div>
												<span>{{post.date}}</span>
												<span>by 
													<a :href="'/#/profile/' + post.author.username ">{{post.author.name}}</a> </span>
												<span><i>in</i> Health</span>
											</div>
										</div>
									</b-card>
								</div>
							</b-col>
							<b-col sm="4" class="sidebar">
								<h3 class="text-center">Recent Articles</h3>
								<ul>
									<li><a href="">Space Ipsum</a></li>
									<li><a href="/">Lorem Ipsum Dolor</a></li>
								</ul>
							</b-col>
						</b-row>
						
						
      		</b-col>
				</b-row>
    </b-container>
  </div>
</template>

<script>
import api from '@/services/api'
export default {
  data: function(){
      return {
			title:'You.io',
			posts:[]
			}
			
  },
	methods:{

	},
	//props:['posts'],
	created(){
		api().get('')
		.then((response)=>{
			console.log(response.data.articles);
			this.posts = response.data.articles
		})
		.catch(function (error) {
			console.log(error);
		});
	} 
}
</script>

<style lang="scss" scoped>
	.home{
		margin-top:20px;
		a:hover{
			text-decoration:none;
		}
		.posts{
			margin-bottom:20px;
		}
	}
  .card-text{
		text-align: left;
	}
</style>




