<template>
  <b-container fluid class="followers">
	  <b-row>
		  <b-col v-if="$store.state.popUserInView.followers.length != 0" sm='12' class="followers-inner" v-for='follower in $store.state.popUserInView.followers' :key='follower._id'>
			  <div class="img">
					<img :src="getPics(follower.imgSrc)" alt="follower_img">
				</div>
				<p><router-link :to="'/profile/' + follower.username">{{follower.name}}</router-link></p>
			  <div class="">
					<button v-if="$store.state.user && $store.state.user.username == follower.username">You</button>
				  <button @click="follow(follower.followers.indexOf($store.state.user._id),follower, $event)" v-if="$store.state.user && $store.state.user.username != follower.username">{{follower.followers.indexOf($store.state.user._id) == -1 ? 'Follow' : 'Following'}}</button>
				  <!--button @click="unfollow" v-if="follower.followers.indexOf($store.state.user._id) == 1" v-show="show">Following</button-->
			  </div>
		  </b-col>
			<b-col v-if="$store.state.popUserInView.followers.length == 0">No followers</b-col>
	  </b-row>
		<hr>
  </b-container>
</template>

<script>
import api from '@/services/api'
export default {
  data(){
	  return {
			
	  }
	},
	methods:{
		async follow(index, follower, event){
			try {
				event.target.setAttribute('disabled', 'disabled')
				if(index == -1){  // to follow a user
					//post('profile/:username/follow/:userId/:loggedInUserId')
					let response = await api().post(`profile/${this.$route.params.username}/follow/${follower._id}/${this.$store.state.user._id}`)
					//console.log(response.data.user2)
					console.log('followed successfully')
					this.$store.dispatch('setUserInView', {user:response.data.user2, populatedUser:response.data.populatedUser, TAfollowing:response.data.updatedFollowingList})
	
					event.target.removeAttribute('disabled')
				}
				else{ // to unfollow a user
					let response2 = await api().post(`profile/${this.$route.params.username}/unfollow/${follower._id}/${this.$store.state.user._id}`)
					//console.log(response2.data.user2)
					console.log('unfollowed successfully')
					this.$store.dispatch('setUserInView', {user:response2.data.user2, populatedUser:response2.data.populatedUser, TAfollowing:response2.data.updatedFollowingList})

					event.target.removeAttribute('disabled')
				}
			} catch (error) {
				console.trace(error)
				console.log(error)
			}
		},
		getPics(url){
			return url
		}
	},
	created(){
		/*api().get('profile/' + this.$route.params.username + '/followers').then(response=>{
			console.log(response.data.populatedUser.followers);
			this.followers = response.data.populatedUser.followers
			this.$parent.user = response.data.populatedUser
			this.$parent.followersCount = response.data.populatedUser.followers.length
			//this.$parent.followingCount = response.data.following.length
		}).catch(error=>{
			console.log(error)
		}) */
	}
}
</script>
<style lang="scss" scoped>
	.followers{
		margin-top:5px;
		.img{
			width:50px;
			height:50px;
			background-color:aquamarine;
			float:left;
			margin-right:10px;
			img{
				width:100%;
				height:50px;
			}
		}
		.followers-inner{
			overflow:auto;
		}
		button{
			float:right;
		}
		button:hover{
			cursor: pointer;
		}
	}
</style>




