<template>
  <div>
		<b-container class="fluid">
				<b-row>
					<b-col sm="10" offset-sm="1" class="article">
						<h3 class='text-center'>{{post.title}}</h3>
						<hr>
						<b-row>
							<b-col sm="10" offset-sm="1" class="main">
								<div class='posts'>
									<b-card class='card' style='background-color:purpl;min-height:200px;' img-src="./static/noImg.png">
										<div class="card-text">
											<p>{{post.body}}</p>
											<p>
												<span><router-link :to="'/profile/' + post.author.username">{{post.author.name}}</router-link></span>
												<span>{{post.date}}</span>
												<button id="like" @click="like(post)" v-if="$store.state.user"><b-badge> {{post.likes.length}}</b-badge>{{post.likes.indexOf(this.$store.state.user._id) == -1 ? '  Like' : '  Liked'}}</button>
											</p>
										</div>
									</b-card>
								</div>
								
								<div class="comments">
									<h4>Comments</h4>
									<div v-for="comment in comments" :key='comment._id'>
										<div>
											<p><span id="title">{{comment.title}}</span>
											<span>
												<i>by</i> 
											<router-link :to="'/profile/' + comment.commenter.username">{{comment.commenter.name}}</router-link>
											</span>
											</p>
											<p>{{comment.body}}</p>
										</div>
										<hr>
									</div>
	
									<h4>Add your comment</h4>
									<p v-if="!$store.state.user">Your must be loggedin to comment</p>
									<b-form @submit.prevent="addComment()" v-if='$store.state.user' enctype='multipart/form-data'>

										<b-form-group>
											<b-form-input id="Input1" type="text"	v-model="comment.title"	required	placeholder="Enter title"></b-form-input>
										</b-form-group>
										<b-form-group>
											<b-form-textarea id="textarea1" v-model="comment.body" placeholder="Write comment" :rows="3" :max-rows="6"></b-form-textarea>
										</b-form-group>
										<b-form-group>
											<b-button type="submit" variant="primary">Submit Comment</b-button>
										</b-form-group>
										
									</b-form>
								</div>
							</b-col>
						</b-row>
						
						
      		</b-col>
				</b-row>
    </b-container>
  </div>
</template>
<script>
import api from '@/services/api'
export default {
  data(){
    return {
				post:{},
				//username:this.post.author.username,
				comments:[],
				comment:{
					title:'',
					body:''
				}
    }
	},
	methods:{
		like(post){
			api().post('articles/' + post._id + '/like/' + this.$store.state.user._id ).then(result=>{
				console.log(result.data)
				this.post = result.data
			})
		},
		addComment(){
			api().post('articles/' + this.post._id, {commenter:this.$store.state.user._id, title:this.comment.title, body:this.comment.body})
			.then(result=>{
				console.log(result.data)
				this.comments = result.data
				this.comment = {}
			})
		}
	},

	created(){
		api().get('articles/' + this.$route.params.id)
		.then((response)=>{
			console.log(response.data.article);
			response.status == 200 ?
			this.post = response.data.article : this.post = '[title:"No data"]'
			this.comments = response.data.comments
		})
		.catch(function (error) {
			console.log(error);
		});
	}
}
</script>
<style lang="scss" scoped>
  .article{
    margin-top: 20px;
    h3{
      color:seagreen;
    }
		#like{
			float:right;
		}
		#like:hover{
			cursor: pointer;
		}
		b-badge{padding-right:2px;}
		#title{
			font-weight: bold;
		}
  }
</style>



