<template>
  <div>
		<b-navbar toggleable="md" type="dark" variant="info">

			<b-navbar-toggle target="nav_collapse"></b-navbar-toggle>

			<b-navbar-brand href="/#/">You.io</b-navbar-brand>

			<b-collapse is-nav id="nav_collapse">

				<b-navbar-nav>
				<b-nav-item href="/">Articles</b-nav-item>
				<b-nav-item href="#">Create</b-nav-item>
				<b-nav-item href="#">Categories</b-nav-item>
				</b-navbar-nav>

				<!-- Right aligned nav items -->
				<b-navbar-nav class="ml-auto">

				<b-nav-form>
					<b-form-input size="sm" class="mr-sm-2" type="text" placeholder="Search"/>
					<b-button size="sm" class="my-2 my-sm-0" type="submit">Search</b-button>
				</b-nav-form>

				<b-nav v-if="!$store.state.loginstatus" class="loginspot">
					<b-nav-item href="/"><router-link :to="'/users/login'">Login</router-link></b-nav-item>
					<b-nav-item href="#"><router-link :to="'/users/register'">Register</router-link></b-nav-item>
				</b-nav>
				
				<b-nav-item-dropdown text="New" right v-if="$store.state.loginstatus">
					<b-dropdown-item href="#"><router-link :to='"/profile/" + $route.params.username + "/new"'>New Article</router-link></b-dropdown-item>
					<b-dropdown-item href="#">New Stuff</b-dropdown-item>
					<b-dropdown-item href="#">Another stuff</b-dropdown-item>
					<b-dropdown-item href="#">FA</b-dropdown-item>
				</b-nav-item-dropdown>

				<b-nav-item-dropdown right v-if='$store.state.loginstatus'>
					<!-- Using button-content slot -->
					<template slot="button-content">
						<em>User</em>
					</template>
					<b-dropdown-item v-bind:href='"/#/profile/" + $store.state.user.username'>{{$store.state.user.username}}</b-dropdown-item>
					<b-dropdown-item href="#">Profile</b-dropdown-item>
					<b-dropdown-item href="#">Notification</b-dropdown-item>
					<b-dropdown-item href="#">Settings</b-dropdown-item>
					<hr>
					<b-dropdown-item href="#">Help</b-dropdown-item>
					<b-dropdown-item href="#" @click="logout">Signout</b-dropdown-item>
				</b-nav-item-dropdown>
				</b-navbar-nav>

			</b-collapse>
		</b-navbar>

  </div>
</template>
<script>
export default {
  data(){
	  return {
		message:'Navbar'
	  }
  },
  methods:{
	  logout(){
		  this.$store.dispatch('logout', 'logout')
		  this.$router.push('/')
	  }
  }
}
</script>
<style lang="scss" scoped>
	.loginspot a{
		color:#fff;
	}
</style>





