<template>
	<b-container class="register">
		<b-row>
			<b-col sm='8' offset-sm="2">
				<h3 class="text-center">Register</h3>
				<b-form @submit.prevent="register" v-if="show" enctype='multipart/form-data'>

					<b-form-group	label="Your Name:"	label-for="Input2">
						<b-form-input id="Input2" type="text"	v-model="form.name"	required	placeholder="Enter name"></b-form-input>
					</b-form-group>
					<b-form-group	label="Your username:"	label-for="Input5">
						<b-form-input id="Input5" type="text"	v-model="form.username"	required	placeholder="Enter username"></b-form-input>
					</b-form-group>

					<b-form-group label="Email address:" label-for="Input1">
						<b-form-input id="Input1" type="email" v-model="form.email" required placeholder="Enter email"></b-form-input>
					</b-form-group>
					<b-form-group>
						<b-form-file @change="handleUpload($event.target.files)"  placeholder="Choose a file..."></b-form-file>
					</b-form-group>
					
					<b-form-group	label="Password:"	label-for="Input3">
						<b-form-input id="Input3" type="password"	v-model="form.password"	required	placeholder="Password"></b-form-input>
					</b-form-group>
					<b-form-group	label="Confirm Password:"	label-for="Input4">
						<b-form-input id="Input4" type="password"	v-model="form.password2"	required	placeholder="Confirm password"></b-form-input>
					</b-form-group>

					<b-button type="submit" variant="primary">Submit</b-button>
				</b-form>
			</b-col>
		</b-row>
	</b-container>
</template>


<script>
import authenticationService from '@/services/authenticationService'
export default {
  data () {
		return {
			form: {
				email: '',
				name: '',
				username:'',
				imgSrc:'',
				password:'',
				password2:''
			},
			show: true
		}
	},
	methods:{
		handleUpload(files) {
		  this.form.imgSrc = files[0];
		  console.log(files[0].name)
        },
		async register(){
			try {
				
				//this.show = !this.show
				//console.log(JSON.stringify(this.form))
				const formData = new FormData()
				formData.append('imgSrc', this.form.imgSrc);
				formData.append('name', this.form.name);
				formData.append('email', this.form.email);
				formData.append('username', this.form.username);
				formData.append('password', this.form.password);
				formData.append('password2', this.form.password2);
				//console.log(formData)

				const result = await authenticationService.register('users/register', formData)
				console.log(result.data);
				this.$store.dispatch('setUser', result.data)
				this.$router.push('/profile/' + result.data.username)
				
			} catch (error) {
				console.trace(`An error occurred during registration ----> ${error}`)
			}
		}
	}
	
}
</script>
<style lang="scss" scoped>
	.register{
		margin-top: 20px;
	}
</style>





