<template>
  <b-container fluid class="following">
	  <b-row>
		  <b-col sm='12' class="following-inner" v-for='following in $store.state.TAfollowing' :key='following._id'>
			  <div class="img">
				  <img :src="getPics(following.imgSrc)" alt="follower_img">
			  </div>
				<p><router-link :to="'/profile/' + following.username">{{following.name}}</router-link></p>
			  <div class="">
					<button v-if="$store.state.user && $store.state.user.username == following.username">You</button>
				  	<button @click="follow(following, $event)" v-if="$store.state.user && $store.state.user.username != following.username">{{following.followers.indexOf($store.state.user._id) == -1 ? 'Follow' : 'Following'}}</button>
			  </div>
		  </b-col>
		  <b-col v-if="$store.state.TAfollowing.length == 0">Not following any yet</b-col>
	  </b-row>
		<hr>
  </b-container>
</template>

<script>
  import api from '@/services/api'
	export default {
		data(){
			return {
				
			}
		},
		methods:{
			async follow(following, event){
				event.target.setAttribute('disabled', 'disabled')
				try {
					if(following.followers.indexOf(this.$store.state.user._id) == -1){  // to follow a user
						//post('profile/:username/follow/:userId/:loggedInUserId')
						let response = await api().post(`profile/${this.$route.params.username}/follow/${following._id}/${this.$store.state.user._id}`)
						this.$store.dispatch('setUserInView', {user:response.data.user2, populatedUser:response.data.populatedUser, TAfollowing:response.data.updatedFollowingList})
						event.target.removeAttribute('disabled')
					}
					else{ // to unfollow a user
						let response2 = await api().post(`profile/${this.$route.params.username}/unfollow/${following._id}/${this.$store.state.user._id}`)
						this.$store.dispatch('setUserInView', {user:response2.data.user2, populatedUser:response2.data.populatedUser, TAfollowing:response2.data.updatedFollowingList})
						event.target.removeAttribute('disabled')
					}
				} catch (error) {
					console.log(error)
				}
			},
			getPics(url){
				return url
			}
		},
		created(){
			
		}
	}
</script>
<style lang="scss" scoped>
	.following{
		margin-top:5px;
		.img{
			width:50px;
			height:50px;
			background-color:aquamarine;
			float:left;
			margin-right:15px;
			img{
				width:100%;
				height:50px;
			}
		}
		.following-inner{
			overflow:auto;
			
		}
		button{
			float:right;
		}
		button:hover{
			cursor: pointer;
		}
	}
</style>




