<template>
  <b-container fluid class="userArticles">
	  <b-row>
		  <b-col v-if="userArticles.length == 0">No articles</b-col>
		  <b-col sm='12' class="articles-inner" v-for="article in userArticles" :key='article._id'>
			  <div class="img">
				  <img :src='getPics(article.imgSrc)' alt="cover_image">
			  </div>
			  <div class="articleDetail">
				  <p>{{article.body.toString().substr(0,150)}}...</p>
			  </div>
			  <hr>
		  </b-col>
	  </b-row>
  </b-container>
</template>
<script>
import api from '@/services/api'
export default {
  data(){
	  return {
			userArticles:[]
	  }
  },
  methods:{
	  getPics(url){
		  return url
	  }
  },
  created(){
   api().get('profile/'  + this.$route.params.username + '/articles').then(response=>{
		 console.log(response.data.articles)
		 this.userArticles = response.data.articles
	 }).catch(error=>{
		 console.log(error)
	 })
  }
}
</script>
<style lang="scss" scoped>
	.articles-inner{
		overflow:auto;
	}
	.userArticles{
		margin-top:5px;
	}
	.img{
		width:70px;
		height:70px;
		background:saddlebrown;
		float: left;
		margin-right: 20px;
	}
	.img img{
		width:100%;
		height: 70px;
	}
	.articleDetail{
		padding:px;
		min-height: 70px;
	}
</style>




