<template>
	<b-container fluid class="bv-example-row dashboard">
		<b-row>
    	<b-col sm="10" offset-sm="1" class='home'>
				<b-row>
					<b-col sm="3" class="profile">
						<img :src="imgSrc" alt="profile image">
						<p>  {{$store.state.popUserInView.username}}</p>
						<p>  {{$store.state.popUserInView.name}}</p>
					
						<button @click="follow($store.state.userInView, $event)" v-if=" $store.state.user && $store.state.user.username != $store.state.userInView.username">{{$store.state.userInView.followers.indexOf(this.$store.state.user._id) == -1 ? 'Follow' : 'Following'}}</button>
						
					</b-col>
					<b-col sm="9" class="about">
						<b-row class="tabs">
							<ul class="list-inline">
								<li><router-link :to="'/profile/' + $route.params.username">Stat</router-link></li>
								<li><router-link :to="'/profile/' + $route.params.username + '/articles'" exact>Articles</router-link></li>
								<li><router-link :to="'/profile/' + $route.params.username + '/followers'" exact>Followers  <b-badge>{{$store.state.popUserInView.followers.length}}</b-badge></router-link></li>
								<li><router-link :to="'/profile/' + $route.params.username + '/following'" exact>Following <b-badge>{{$store.state.TAfollowing.length}}</b-badge></router-link></li>
								<li><a href="/profile/">Friends</a></li>
							</ul>
							<hr>
						</b-row>
						<b-row>

							<!-- Outlet for children routes -->
							<router-view></router-view>
						</b-row>
					</b-col>
				</b-row>
			</b-col>
		</b-row>
	</b-container>
</template>

<script>
import UserArticles from '@/components/UserArticles'
import api from '@/services/api'

export default {
	data(){
		return {
			imgSrc:this.$store.state.popUserInView.imgSrc,
		}
	},
	computed:{
	},
	components:{
		UserArticles:UserArticles
	},
	methods:{
		async follow(user, event){
			
			try {
				console.log(user)
				event.target.setAttribute('disabled', 'disabled')
				if(user.followers.indexOf(this.$store.state.user._id) == -1){  // to follow a user
					//post('profile/:username/follow/:userId/:loggedInUserId')
					let response = await api().post(`profile/${this.$route.params.username}/follow/${user._id}/${this.$store.state.user._id}`)
					console.log('followed successfully')
					this.$store.dispatch('setUserInView', {user:response.data.user2, populatedUser:response.data.populatedUser, TAfollowing:response.data.updatedFollowingList})
					event.target.removeAttribute('disabled')
				}
				else{ // to unfollow a user
					let response2 = await api().post(`profile/${this.$route.params.username}/unfollow/${user._id}/${this.$store.state.user._id}`)
					console.log('unfollowed successfully')
					this.$store.dispatch('setUserInView', {user:response2.data.user2, populatedUser:response2.data.populatedUser, TAfollowing:response2.data.updatedFollowingList})
					event.target.removeAttribute('disabled')
				}
			} catch (error) {
				console.trace(error)
				console.log(error);
			}
		}
	},
	created(){
		api().get('profile/' + this.$route.params.username).then(response=>{
			//console.log(response.data)
			//this.followStatus = response.data.user.followers.indexOf(this.$store.state.user._id) == -1 ? 'Follow' : 'Following'
			this.$store.dispatch('setUserInView', response.data)
		}).catch(error=>{
			console.log(error)
		})
	}
}
</script>
<style lang="scss" scoped>
.dashboard{
    background-color:ta;
	margin-top:20px;
    padding-left:0px;
    padding-right: 0px;

	.profile{
		background-color:seagreen;
		color:#fff;
		height:320px;
	}
	.profile .img{
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		height:120px;
		border-radius: 4px;
	}
	.profile img{
		width:100%;
		height:120px;
		display:block;
		border-radius:4px;
	}
	.about{
		background-color:azur;
		min-height:100px;
	}

	.tabs ul li{
		background:gree;
		display:inline-block;
		margin-right:10px;
		padding:5px;
	}
	.tabs ul li a{
		padding:5px;
		color:#586069;
	}
	.tabs{border-bottom:1px solid #eee;}
	.wel{
		border:1px solid #d8d0d0;
		border-radius: 4px;
		padding:15px;
	}
}
</style>





